import { Component, OnDestroy, OnInit } from '@angular/core';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import {
    FormControl,
    FormGroup,
    MaxValidator,
    Validators,
} from '@angular/forms';
import { CobranzasService } from '../../services/cobranzas.service';
import { MatTableDataSource } from '@angular/material/table';
import {
    debounceTime,
    distinctUntilChanged,
    Subject,
    Subscription,
    takeUntil,
} from 'rxjs';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { environment } from 'environments/environment';
import { max } from 'lodash';

interface Cobro {
    descripcion_forma_cobro: string;
    idcobrotipo: number;
    importe_neto_a_descontar: number;
    importe_pago_real: number;
    importe_interes: number;
    importe_descuento: number;
}

@Component({
    selector: 'app-calculadora-cobros',
    templateUrl: './calculadora-cobros.component.html',
})
export class CalculadoraCobrosComponent implements OnInit, OnDestroy {
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    totalDescontar: number;
    totalPagosReales: number;
    totalIntereses: number;
    totalDescuentos: number;
    saldoPagar: number;

    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';

    netoDescontarSub: Subscription;
    pagoRealSub: Subscription;

    form: FormGroup;
    metodosCobro;
    ultimoModificado;
    displayedColumns = [
        'descripcion_forma_cobro',
        'importe_neto_a_descontar',
        'importe_pago_real',
        'importe_interes',
        'importe_descuento',
        'acciones'
    ];
    public dataSource: MatTableDataSource<Cobro> = new MatTableDataSource();
    private token = localStorage.getItem('accessToken');

    constructor(
        private alert: AppAlertService,
        private router: Router,
        private _cobranzasService: CobranzasService,
        public _headerTextService: HeaderTextService,
        private _empresaService: EmpresaService
    ) {
        this._headerTextService.setTitulo(this.router);
    }

    ngOnInit(): void {
        this.newForm();
        this.getMetodosCobro();

        this.netoDescontarSub = this.form
            .get('netoDescontar')
            ?.valueChanges.subscribe(() => {
                this.ultimoModificado = 'netoDescontar';
                if (this.form.get('netoDescontar')?.value) {
                    this.form.get('pagoReal').disable({ emitEvent: false });
                } else {
                    this.form.get('pagoReal').enable({ emitEvent: false });
                }

                this.calcularValores();
            });

        this.pagoRealSub = this.form
            .get('pagoReal')
            ?.valueChanges.subscribe(() => {
                this.ultimoModificado = 'pagoReal';
                if (this.form.get('pagoReal')?.value) {
                    this.form
                        .get('netoDescontar')
                        .disable({ emitEvent: false });
                } else {
                    this.form.get('netoDescontar').enable({ emitEvent: false });
                }

                this.calcularValores();
            });

        this.observePorcentajes();

        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe((empresa) => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
    }

    newForm() {
        this.form = new FormGroup({
            totalVenta: new FormControl(0, Validators.required),
            bonificado: new FormControl(
                { value: 0, disabled: true },
                [Validators.max(100), Validators.min(0)]
            ),
            noBonificado: new FormControl(
                { value: 0, disabled: true },
                [Validators.max(100), Validators.min(0)]
            ),
            presupuesto: new FormControl(
                { value: 0, disabled: true },
                Validators.required
            ),
            saldoDescontar: new FormControl({ value: 0, disabled: true }),
            metodoCobro: new FormControl(-1, Validators.required),
            netoDescontar: new FormControl(0, Validators.required),
            pagoReal: new FormControl(0, Validators.required),
        });
    }

    /* Observa los cambios en los inputs de porcentajes */
    observePorcentajes() {
        this.form
            .get('totalVenta')
            .valueChanges.pipe(distinctUntilChanged(), debounceTime(750))
            .subscribe((value) => {
                this.calcularPorcentajes('total');
            });
        this.form
            .get('bonificado')
            .valueChanges.pipe(distinctUntilChanged(), debounceTime(750))
            .subscribe((value) => {
                this.calcularPorcentajes('bonificado');
            });
        this.form
            .get('noBonificado')
            .valueChanges.pipe(distinctUntilChanged(), debounceTime(750))
            .subscribe((value) => {
                this.calcularPorcentajes('noBonificado');
            });
    }

    /* Calcula los porcentajes de los campos de manera dinamica y calcula
     el presupuesto en base al porcentaje del total de la venta y el porcentaje no bonificado */
    calcularPorcentajes(opcion: string) {
        let noBonificado = 100 - this.form.get('bonificado').value;
        let bonificado = 100 - this.form.get('noBonificado').value;
        if(this.form.get('bonificado').value > 100 || this.form.get('bonificado').value < 0 || this.form.get('noBonificado').value > 100 || this.form.get('noBonificado').value < 0){
            this.alert.warning('Los porcentajes no pueden ser mayores a 100 ni menores a 0')
            this.form.get('bonificado').setValue(50, { emitEvent: false });
            this.form.get('noBonificado').setValue(50, { emitEvent: false });
            let valorNoBonificado =
                (this.form.get('noBonificado').value *
                    this.form.get('totalVenta').value) /
                100;
            this.form.get('presupuesto').setValue(valorNoBonificado);
        } else {
            switch (opcion) {
                case 'total':
                    this.form.get('bonificado').enable();
                    this.form.get('bonificado').setValue(50, { emitEvent: false });
                    this.form.get('noBonificado').enable();
                    this.form
                        .get('noBonificado')
                        .setValue(50, { emitEvent: false });
                    break;
                case 'bonificado':
                    this.form.get('noBonificado').setValue(noBonificado);
                    break;
                case 'noBonificado':
                    this.form.get('bonificado').setValue(bonificado);
                    break;
            }
            let valorNoBonificado =
                (this.form.get('noBonificado').value *
                    this.form.get('totalVenta').value) /
                100;
            this.form.get('presupuesto').setValue(valorNoBonificado);
        }
    }

    /* Obtiene los Métodos de Cobro disponibles */
    getMetodosCobro() {
        this._cobranzasService.getMetodosCobro(this.token).subscribe({
            next: (res: any) => {
                this.metodosCobro = res.result;
            },
            error: (err) => {
                console.error(err);
            },
        });
    }

    imprimirCobros() {
        if (
            this.form.get('presupuesto').value &&
            this.dataSource.data.length > 0
        ) {
            let hoy = new Date();
            let payload = {
                cobro: {
                    fecha: `${hoy.getFullYear()}-${
                        hoy.getMonth() + 1
                    }-${hoy.getDate()}`,
                    presupuesto: this.form.get('presupuesto').value,
                },
                items_forma_cobro: this.dataSource.data,
            };

            this._cobranzasService
                .generarReporteCalculadora(this.token, payload)
                .subscribe({
                    next: (res: any) => {
                        window.open(
                            environment.baseRest + '/' + res.path,
                            '_blank'
                        );
                    },
                    error: (err) => {},
                });
        } else {
            this.alert.warning('Debe completar todos los campos!');
        }
    }

    /* Realiza el cálculo de los valores de Neto a Descontar y 
       Pago Real teniendo en cuenta el Método de Pago */
    calcularValores() {
        if (this.form.get(this.ultimoModificado)?.value) {
            if (this.form.get('metodoCobro').value != -1) {
                if (
                    this.form.get('netoDescontar').value ||
                    this.form.get('pagoReal').value
                ) {
                    let payload = {
                        idcobrotipo: this.form.get('metodoCobro').value,
                        importe_pago_real:
                            this.ultimoModificado === 'pagoReal'
                                ? this.form.get('pagoReal').value
                                : null,
                        importe_neto_a_descontar:
                            this.ultimoModificado === 'netoDescontar'
                                ? this.form.get('netoDescontar').value
                                : null,
                    };

                    this._cobranzasService
                        .calcularImporte(this.token, payload)
                        .subscribe({
                            next: (res: any) => {
                                if (res.result) {
                                    let netoDescontar = res.result
                                        .importe_neto_a_descontar
                                        ? res.result.importe_neto_a_descontar
                                        : null;
                                    let pagoReal = res.result.importe_pago_real
                                        ? res.result.importe_pago_real
                                        : null;

                                    netoDescontar
                                        ? this.form
                                              .get('netoDescontar')
                                              .setValue(netoDescontar, {
                                                  emitEvent: false,
                                              })
                                        : '';

                                    pagoReal
                                        ? this.form
                                              .get('pagoReal')
                                              .setValue(pagoReal, {
                                                  emitEvent: false,
                                              })
                                        : '';

                                    this.form
                                        .get('pagoReal')
                                        .enable({ emitEvent: false });
                                    this.form
                                        .get('netoDescontar')
                                        .enable({ emitEvent: false });
                                }
                            },
                            error: (err) => {
                                console.error(err);
                            },
                        });
                } else {
                    this.alert.warning('Debe ingresar un valor');
                }
            } else {
                this.alert.warning('Debe seleccionar un Método de Cobro');
            }
        } else {
            this.alert.warning('Debe ingresar un valor al modificar');
        }
    }

    /* Realiza el cálculo de los valores del detalle de cada item */
    calcularDetalle() {
        this.totalDescontar = this.dataSource.data.reduce(
            (acc, element) => acc + element.importe_neto_a_descontar,
            0
        );
        this.totalPagosReales = this.dataSource.data.reduce(
            (acc, element) => acc + element.importe_pago_real,
            0
        );
        this.totalIntereses = this.dataSource.data.reduce(
            (acc, element) => acc + element.importe_interes,
            0
        );
        this.totalDescuentos = this.dataSource.data.reduce(
            (acc, element) => acc + element.importe_descuento,
            0
        );
        this.saldoPagar =
            this.form.get('presupuesto').value - this.totalDescontar;

        this.form
            .get('saldoDescontar')
            .setValue(this.saldoPagar);

        this.form
            .get('netoDescontar')
            .setValidators([
                Validators.max(this.form.get('saldoDescontar').value),
                Validators.min(0),
                Validators.required,
            ]);
        this.form.get('netoDescontar').updateValueAndValidity();
    }

    /* Carga de Cobros */
    cargarItemCobro() {
        let mismaFormaCobro = this.dataSource.data.find(e => e.idcobrotipo === this.form.get('metodoCobro').value);

        if(mismaFormaCobro){
            this.alert.warning('No puede agregar dos veces el mismo metodo de cobro')
        } else {
            if (this.form.valid && this.form.get('metodoCobro').value != -1) {
                let payload = {
                    idcobrotipo: this.form.get('metodoCobro').value,
                    importe_pago_real:
                        this.ultimoModificado === 'pagoReal'
                            ? this.form.get('pagoReal').value
                            : null,
                    importe_neto_a_descontar:
                        this.ultimoModificado === 'netoDescontar'
                            ? this.form.get('netoDescontar').value
                            : null,
                };
    
                this._cobranzasService
                    .cargarItemCobroCalculadora(this.token, payload)
                    .subscribe({
                        next: (res: any) => {
                            this.dataSource.data = [
                                ...this.dataSource.data,
                                res.result,
                            ];
                            this.resetForm();
                            this.calcularDetalle();
                        },
                        error: (err) => {},
                    });
            } else {
                this.alert.warning('Verifique los campos del formulario');
            }
        }
        
    }

    removeItem(item){
        this.saldoPagar += item.importe_neto_a_descontar;
        this.dataSource.data = this.dataSource.data.filter(e => e.idcobrotipo != item.idcobrotipo);
        this.calcularDetalle();
    }

    resetForm(){
        this.form.get('metodoCobro').setValue(-1, {emitEvent: false})
        this.form.get('netoDescontar').setValue(0, {emitEvent: false})
        this.form.get('pagoReal').setValue(0, {emitEvent: false})
    }

    ngOnDestroy(): void {
        this.pagoRealSub.unsubscribe();
        this.netoDescontarSub.unsubscribe();
    }
}
